<template functional>
	<header class="section-masthead">
		<section class="section-masthead__text">
			<h1>{{props.title}}</h1>
			<p class="subtitle">{{props.subtitle}}</p>
		</section>
	</header>
</template>

<script>
export default {
	name: 'Masthead',
	props: {
		title: {
			type: String,
			required: true
		},
		subtitle: {
			type: String,
			required: true
		}
	}
};
</script>

<style scoped>
	
	.section-masthead {
		height: 100vh;
		display: flex;
		align-items: center;
		margin-top: 2rem;
		position: relative;
	}
	
	.section-masthead__text {
		padding: 1em 1.5em;
		background-color: var(--color__dark-alpha);
		transform: translateX(-10%);
	}
	
	.section-masthead::before {
		content: '';
		width: 10%;
		height: 101%;
		background-color: var(--color__complementary);
		opacity: 0.5;
		mix-blend-mode: multiply;
		transform: rotate(0.5deg);
	}
	
	img {
		height: 100%;
		display: block;
		width: 100%;
		object-fit: cover;
	}
	
	@media screen and ( max-width: 64em ) {
		.section-masthead {
			margin-right: -3vw;
			margin-left: -3vw;
		}
	}

</style>



