			</router-link>
			<span>and</span>
			<router-link to="/bayer-lasonil">
				<span>Lasonil</span>
			</router-link><span>),</span>
			<router-link to="/peroni">
				<span>Peroni</span>
			</router-link><span>,</span>
			<router-link to="/tempo">
				<span>Tempo</span>
			</router-link>
			<span>and</span>
			<router-link to="/yamaha">
				<span>Yamaha</span>
			</router-link>
		</p>
	</article>
</template>

<script>
export default {
	name: 'MainCopy',
	props: {
		classes: {
			type: String,
			required: false,
			default: ''
		}
	}
};
</script>

<style scoped>
	p {
		font-size: calc(3vw * var(--font-scaling-ratio));
		line-height: 1.7;
	}
</style>



