<template>
	<section class="media-gallery">
		<h2 class="media-gallery__title">Media gallery</h2>
		<div class="media-gallery__content">
			<video-wrapper v-for="(video, index) in videos"
				class="media-gallery__video"
				:key="index"
				:src="video"
				:title="title"></video-wrapper>
			<lazy-image v-for="(image, i) in images"
				class="media-gallery__img"
				:key="'a' + i"
				:src="image"
				:alt="title"></lazy-image>
		</div>
	</section>
</template>

<script>
import VideoWrapper from './VideoWrapper';
import LazyImage from './LazyImage';

export default {
	name: 'MediaGallery',
	props: {
		title: {
			type: String,
			required: false,
			default: ''
		},
		videos: {
			type: Array,
			required: false,
			default: () => []
		},
		images: {
			type: Array,
			required: false,
			default: () => []
		}
	},
	components: {
		VideoWrapper,
		LazyImage
	}
};
</script>

<style scoped>
	.media-gallery {
		background: linear-gradient(-5deg, rgba(41, 140, 160, 0.1) 55%, rgba(255, 255, 255, 0.5) 0%);
		background-size: 100% 90%;
		position: relative;
		width: 100vw;
		left: 50%;
		right: 50%;
		margin-left: -50vw;
		margin-right: -50vw;
		padding: var(--container-padding);
	}
	/*
	linear-gradient(135deg, rgba(125,185,232,1) 45%,rgba(30,87,153,1) 45%);*/
	.media-gallery__content {
		display: grid;
		grid-gap: 1em;
		grid-template-columns: 60% 40%;
		grid-template-rows: 50% 50%;
	}
	
	.media-gallery__img {
		grid-row: 2;
		width: 100%;
	}
	
	.media-gallery__img + .media-gallery__img {
		transform: translateY(-50%);
	}
	
	.media-gallery__img:only-of-type {
		grid-column: 2;
	}
</style>



