	<div id="app" class="app-wrapper">
		<menu-list/>
		<app-header/>
		<transition mode="out-in">
			<router-view/>
		</transition>
		<app-footer/>
	</div>
</template>

<script>
import AppHeader from '@/components/AppHeader';
import AppFooter from '@/components/AppFooter';
import MenuList from '@/components/MenuList';

export default {
	name: 'App',
	components: {
		AppHeader,
		AppFooter,
		MenuList,
	},
};
</script>



